import { Component, OnInit } from '@angular/core';
import { SaldoService } from '../../service/saldo.service';
import { IgetUser } from '../../models/userget';
import { GetUserService } from '../../service/get-user.service';
import tokenGet from '../../helpers/get.id';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {
  usersList: IgetUser[];
  user: string;

  constructor(
    private getUserService: GetUserService,
    private saldoService: SaldoService
  ) {}

  ngOnInit(): void {
    this.getUserService.getIdUser().subscribe(
      (usersFromApi: IgetUser[]) => {
        this.usersList = usersFromApi;
        console.log(this.usersList);
      },
      (error) => console.error(error)
    );
    this.saldoService.newSaldo(3002, 0).subscribe((saldo) => console.log());
  }
  onSaldo(): void {
    this.user = tokenGet();
    if (this.usersList.length > 0) {
      for (let i = 0; i < this.usersList.length; i++) {
        if (this.usersList[i].nomUsuario === this.user) {
          const idUser = this.usersList[i].id;
        }
      }
    }
  }
}
